.skill_block {
    border: 1px solid #474747;
    border-radius: 10px;

    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 250px;
    padding: 20px;

}

.skill h3 {
    color: #fff;
    font-size: 1.75rem;
    font-weight: 600;
    line-height: 1.2;
}

.skill_description {
    color: #b0aac0;
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    line-height: 1.5;

}