.project_block{
    border-radius: 12px;
    width: 400px;
    background-color: #2F2F2F;
}

.project_img {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 200px;

    border-top-left-radius: 12px;
    border-top-right-radius: 12px;

    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.project_img:before {
    position: absolute;
    content: '';
    background-color: black;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.5;


}

.project_btn {
    position: relative;
    z-index: 1;

    font-size: 14px;

    padding: 12px 30px;
    background-color: #009e66;
    /*background-color: transparent;*/
    color: #fff;
    font-weight: bold;

    border-radius: 5px;
    font-family: 'Poppins', sans-serif;
  

    text-decoration: none;
    transition: 0.3s;

    border: none;
    opacity: 0.6;

}

.project_btn:hover {
    cursor: pointer;
    opacity: 1;
}

.project_info {
    padding: 20px;
}

.project_title {
    font-family: 'Poppins', sans-serif;
    color: #fff;
    font-size: 28px;
    margin-top: 0;
    margin-bottom: 18px;
    font-weight: 600;
    line-height: 1.2;
}

.project_description {
    color: #b0aac0;
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    line-height: 1.5;

}


