

.skillsBlock {
    min-height: 100vh;
    padding: 100px;
    background-color: #2a2a2a;
}



.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
}


.skills_block {
    gap: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;

}



@media (max-width: 650px) and (max-height: 900px){

    .skills {
        display: flex;
        flex-direction: row;
        justify-content: center;
        flex-wrap: wrap;
        width: 100%;

    }
}

