.block_title {
    text-align: center;
    margin-bottom: 72px;
    position: relative;
}

.block_title h2 {
    color: #fff;
    line-height: 1;
    font-size: 24px;
    text-transform: uppercase;
    margin: 0;
}

.block_title h2:before, .block_title h2:after {
    content: '';
    width: 0;
    height: 0;
    border: 8px solid transparent;
    border-bottom-color: #009e66;
    position: absolute;
    top: 2.5px;

}

.block_title h2:before {
    border-left-color: #009e66;
    right: -30px;
}

.block_title h2:after {
    border-right-color: #009e66;
    left: -30px;
}
